import { useMemo } from 'react';
import { motion } from 'framer-motion';

export type StepperProps = {
  currentStep: number;
  maxStep: number;
};

const Stepper = ({ currentStep, maxStep }: StepperProps) => {
  const width = useMemo(() => {
    return maxStep > 0 ? (100 * currentStep) / maxStep : 0;
  }, [currentStep, maxStep]);

  return (
    <div className="qb-stepper flex items-center relative w-full h-0.5 bg-gray-200">
      <motion.div
        className="qb-stepper__track flex h-0.5 bg-primary-500"
        style={{ width: `${width}%` }}
        animate={{ width: `${width}%` }}
        transition={{ duration: 0.5 }}
      />
    </div>
  );
};

export default Stepper;
